/* eslint-disable react/jsx-one-expression-per-line */
/* eslint-disable react/no-unescaped-entities */
import React from "react";
import Helmet from "react-helmet";
import { graphql } from "gatsby";
import Layout from "../layout";
import PostListing from "../components/PostListing/PostListing";
import SEO from "../components/SEO/SEO";
import config from "../../data/SiteConfig";

class Index extends React.Component {
  render() {
    const postEdges = this.props.data.allMarkdownRemark.edges;
    return (
      <Layout>
        <div className="container">
          <Helmet title={config.siteTitle} />
          <SEO />
          <div className="hero">
            <img className="logo" src="/icons/logo.svg" alt="ezfire logo" />
            <h1 className="logotype">ezfire</h1>
            <h3>why ezfire?</h3>
            <ol>
              <li>easy and unique</li>
              <li>LOL username</li>
            </ol>
          </div>
          <div className="columns two">
            <div className="panel">
              <h2>
                Who am I? 
              </h2>
              <p>
              My name's Edwin Zamudio. I'm a student. I like learning to make things like this website.
              </p>
            </div>
            <div className="panel">
              <h2>
                I am currently...
              </h2>
              <p>
                studying computer science at <a href="https://www.iit.edu">Illinois Tech</a>.
              </p>
            </div>
          </div>
          <div className="columns three">
            <div className="panel">
              <h2>Latest Articles</h2>
              <PostListing postEdges={postEdges} />
            </div>
            <div className="panel">
              <h2>Latest Work</h2>
              <p>Thumbnail</p>
            </div>
            <div className="panel">
              <h2>Reading...</h2>
              <p>Thumbnail</p>
            </div>
          </div>
        </div>
      </Layout>
    );
  }
}

export default Index;

/* eslint no-undef: "off" */
export const pageQuery = graphql`
  query IndexQuery {
    allMarkdownRemark(
      limit: 2000
      sort: { fields: [fields___date], order: DESC }
    ) {
      edges {
        node {
          fields {
            slug
            date
          }
          excerpt
          timeToRead
          frontmatter {
            title
            tags
            cover
            date
          }
        }
      }
    }
  }
`;
